@import url('https://fonts.googleapis.com/css2?family=Baloo+Bhai+2&family=Fira+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Baloo+Bhai+2&family=Fira+Sans&family=Source+Code+Pro:ital,wght@0,300;1,300&display=swap');
* {
    margin: 0;
    padding: 0;
    color: rgb(187, 185, 185);
    margin-bottom: 0px;
}
html{
    margin-bottom: 0px;
    height: 100vh;
}
body{
   /* background-color: rgb(12, 12, 11); */
   background-color: #0e141b;
   height: 100vh;
}
.progressBar{
    height: 1px;
    width: 12px;
    /* background-color: red; */
    animation: progressBar  1.5s 1 ;
}
@keyframes progressBar {
    0%{
        background-color: red;

    }
    100%{
        background-color: red;
        width: 100%;
    }
}
.navbar{
    width: 100vw;
    /* padding: 0px; */
    text-decoration: none;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0px;
    font-family: 'Baloo Bhai 2', cursive;
    /* background-color: rgb(49, 47, 49); */
    background-color: rgb(24, 21, 28);
    height: 12vh;
}
.navbar_ul{
    display: flex;
    font-family: 'Baloo Bhai 2', cursive;
    padding-left: 1px;
}
.navbar_ul_li{
    list-style: none;
    font-size: 1.5rem;
    text-decoration: none;
    display: flex;
}
.navbar_ul_li_a{
    color: white;
    display: block;
    padding:  8px;
    border-radius: 50px;
    text-decoration: none;
}
.navbar_ul_li_a:hover {
  font-size: 1.55rem;
  /* background-color: rgb(65, 65, 65) */

}
.nav-link{
    font-family: 'Baloo Bhai 2', cursive;
}
.main {
    /* background-color: yellow; */
    width: 90vw;
    height: 55vh;
    display: flex;
    /* padding: 24px; */
}
.intro{
    margin: 6vh 7vw;
    color: rgb(187, 185, 185);
}
.introh2{
    font-family: 'Fira Sans';
    margin: 12px;
}
.intro-p{
    font-family: 'Arial Narrow Bold', sans-serif;
    margin: 6vh;
    font-size: 1.2rem;
}
.infocontainer {
    width: 80vw;
    margin: 10px auto;
    margin-top: 10vh;
    margin-bottom: 50px;
    flex-wrap: wrap;
}
.myinfo {
    font-size: 0.6em;
    /* display: flex; */
    flex-direction: column;
    justify-content: center;
    font-family: 'Source Code Pro', monospace;
}
.hello {
    font-size: 3.3em;
    font-weight: bold;
    font-family: 'Fira Sans', sans-serif;
    /* color: rgb(145, 20, 20); */
}
.purple{
    font-size: 1em;
    font-weight: bold;
    font-family: 'Fira Sans', sans-serif;
    color: rgb(146, 70, 146);
}
.secondHello{
    font-size: 3.01em;
    font-weight: bold;
    font-family: 'Fira Sans', sans-serif;
    color: rgb(193, 177, 177);
}
.Animate{
    animation: AnimateText 2.8s ease forwards 1 alternate ;
    
}
@keyframes AnimateText {
    from {color: rgb(12, 12, 11);;}
    to {color: rgb(203, 188, 188);;}
  }
.AnimateSecond{
    animation: AnimateTextSecond 3.2s ease-in-out forwards 1 alternate ;
    
}
@keyframes AnimateTextSecond {
    from {color: rgb(12, 12, 11);;}
    to {color: rgb(172, 152, 152);;}
  }
.about {
    font-size: 2.5em;
}
.buttons {
    margin-top: 10px;
    font-size: 16px;
    margin-top: 11vh;
}
.btns {
    padding: 4px 7px;
    border-radius: 7px ;
    color: white;
    background: hsl(224, 42%, 13%);
    font-size: 1.3em;
    margin: 0 3px;
    font-weight: bold;
    cursor: pointer;
}
.btns:hover {
    background-color: rgb(15, 11, 11);
}
.mypic img {
    height: 50vh;
    margin-bottom: 4vh;
}
.contactform {
    padding: 0 34px;
    padding-top: 10px;
    margin:5px 90px;
    color: white;
}

.contactform-h1 {
    font-family: 'Source Code Pro', monospace;
    font-weight: bold;
    color: rgb(187, 185, 185);
 }
.contactform form div {
    padding: 9px 0;
}
#emailHelp {
    font-size: 11px;
    color: midnightblue;
    margin: 0 5px;
}
.mb-3 {
    display: flex;
    flex-direction: column;
}
.form-control {
    width: 25vw;
    border-radius: 5px;
    border-color: black;
    font-size: 18px;
    padding: 2px;
    
}
.btn-sm {
    width: 85px;
    font-size: 10px;
    padding: 6px 8px;
    margin: 10px 0;
}
.btn:hover {
    background-color: white;
    color: hsl(221deg 57% 64%);
}

.projects{
    color: rgb(247, 246, 246);
    text-decoration: none;
    font-size: 2.6rem;
    padding: 9px;
}
.projects p{
    color: rgb(247, 246, 246);
    text-decoration: none;
    margin-top: 7vh;
    font-size: 1.7rem;
}
.projects p a{
    color: rgb(23, 101, 219);
    text-decoration: none;

}
.proj{
    text-decoration: none;
    
}
@media (max-width: 1000px){
    .navbar{
        width: 100vw;
        height: 6vh;
        /* padding: 0.9px; */
    }
    .navbar_ul{
        padding-right: px;
        /* margin-right: 10px; */
    }
    .navbar_ul_li {
        font-size: 1.05rem;
    }
    .navbar_ul_li :hover {
        font-size: 1.16rem;
    }
    .navbar_ul_li_a{
        font-size: 1.1rem;
    }
    .mypic img {
        height: 48vh;
        margin-bottom: 4vh;
    }
    .intro {
        margin: 2vh 0vw;
    }
    .introh2 {
        margin-left: 30px;
        padding: 2px;
    }
    .intro-p {
        margin: 0%;
        margin-left: 6vw;
        font-size: 1.2rem;
    }
    .intro h2{
        margin-top: 29px;
    }
    .projects{
        padding-left: 19px;
        
    }
    .projects p{
        font-size: 1.22rem;
        
    }
    .navbar_ul_li_a:hover {
        font-size: 1.01 rem;
        /* background-color: rgb(65, 65, 65); */
        border-radius: 20px;
        
      }
      .hello{
        font-size: 2.31rem;
      }
      .secondHello{
        font-size: 1.6rem;
      }
}